.main-container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}

.search {
    outline: outset;
    outline-color:#023e8a ;
    padding: 20px 4%;
    border-radius: 20px;
    border: none;
    margin-bottom: 7%;
    background: rgba(250, 250, 250, 0.85);
    font-size: 20px ;
    color: #023e8a;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.city {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 40px 8%;
    border-radius: 20px;
    background: rgba(225, 224, 224, 0.881);
    box-shadow: 10px 10px 5px 0px rgba(15, 15, 15, 0.404);
    outline: outset;
    outline-color:#023e8a ;
}

p {
    margin-top: 10px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-weight: 600;
    
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.city-temp {
    font-size: 5rem;
    font-weight: bold;
    margin-top: 10px;
    color: #1e2432;
    text-align: center;
}

.city sup {
    font-size: 0.5em;
}

.city-name {
    font-size: 2em;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.city-name sup {
    padding: 0.2em 0.6em;
    margin-left: 0.2em;
    border-radius: 30px;
    color: #fff;
    background: #ff8c00;
}

.city-icon {
    margin-top: 10px;
    width: 100px;
    height: 100px;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media only screen and (max-width: 600px) {
    .search {
        padding: 20px 4%;
    }

    .city {
        padding: 40px 20%;
    }
}